import request from './request'

export function addCategory(data){
  return request({
    url: 'category/add',
    method: 'post',
    data: data
  })  
}

export function Categories(){
  return request({
    url: 'categories',
    method: "post"
  })
}

// 获取单条数据
export function GetCategory(id){
  return request({
    url: `category/${id}`,
    method: "get"
  })
}

export function UpdateCategory(id, data){
  return request({
    url: `category/${id}`,
    data: data,
    method: "put"
  })
}

export function DeleteCategory(id){
  return request({
    url: `category/${id}`,
    method: "delete"
  })
}