<template>
  <div>
    <h1>新建分类</h1>

    <a-form :form="form" :label-col="{span: 3}" :wrapper-col="{ span: 12 }" @submit="handleSubmit">
      <a-form-item label="上级分类">
        <a-select style="width: 180px" v-decorator="['parent']">
          <a-select-option v-for="(item) in data" :key="item._id" :value="item._id">{{item.name}}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="名称">
        <a-input v-decorator="['name', { rules: [{ required: true, message: '请输入分类名' }] }]" />
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 3}">
        <a-button type="primary" html-type="submit">提交</a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
import { addCategory, Categories } from "network/category.js";

export default {
  created() {
    this.requestData();
  },
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      data: []
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          addCategory(values)
            .then(res => {
              if (res) {
                this.$notification.success({
                  message: "成功！",
                  description: "成功添加新分类",
                  duration: 2
                });
                console.log(res);
              }
            })
            .catch(() => {
              this.$notification.error({
                message: "出错！",
                description: "已有同名分类",
                duration: 2
              });
            });
        }
      });
    },

    requestData() {
      Categories().then(res => {
        this.data = res;
      });
    }
  }
};
</script>

<style scoped>
h1 {
  font-weight: bold;
}
</style>
